<script>
    import ElButton from "../../node_modules/element-ui/packages/button/src/button";
    import axios from 'axios'
    import util from '../common/js/util'
    import {currVersion} from '../api/api';

    export default {
        data() {
            return {
                systemVersion: '',
                systemName: '',
                systemUpdateTime: '',
                LAST_SYSTEM_UPDATE_TIME: '',
                LAST_SYSTEM_VERSION: ''
            }
        },
        methods: {
            openQq() {
                window.open("http://wpa.qq.com/msgrd?v=3&uin=582866070&site=qq&menu=yes","_blank");
            },
            website() {
                window.open("http://www.lxinet.com","_blank");
            },
            getNewVersion(){
                var _this = this;
                axios.get('http://www.jeesns.cn/newVersion?callback=?').then(function (response) {
                    var data = response.data;
                    data = data.substring(2,data.length-1);
                    data = eval('('+data+')');
                    _this.LAST_SYSTEM_UPDATE_TIME = data.LAST_SYSTEM_UPDATE_TIME;
                    _this.LAST_SYSTEM_VERSION = data.LAST_SYSTEM_VERSION;
                });
            },
            getCurrVersion(){
                var params = new URLSearchParams();
                params.append("token", sessionStorage.getItem("token"));
                currVersion(params).then((res) => {
                    this.systemVersion = res.systemVersion;
                    this.systemName = res.systemName;
                    this.systemUpdateTime = res.systemUpdateTime;
                });
            },
        },
        mounted() {
            this.getNewVersion();
            this.getCurrVersion();
        }
    }

</script>

<style scoped>
    a {
        text-decoration: none;
        color: #58B7FF;
    }
    .grid-content {
        min-height: 36px;
        border: solid 1px #58B7FF;
        border-radius: 5px;
        margin-top: 10px;
        padding: 10px;
    }
</style>


